table {
  text-align: left;
}
th,
td {
  padding: 8px 12px;
  text-align: left;
  border-bottom: 1px solid black;
}

tbody {
  display: block;
}

thead,
tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed; /* even columns width , fix width of table too*/
}

.table-wrapper {
  overflow-x: auto;
  width: 100%;
  height: 100%;
  display: flex;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.table-row.loading {
  width: 100%;
}

.table {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid;
}

.table-header-wrapper {
  display: flex;
}

.table-header {
  display: flex;
  border-bottom: 1px solid;
}

.table-header-cell {
  flex-shrink: 0;
  width: 100px;
  padding: 8px 4px;
}

.table-body-container {
  display: flex;
}

.table-body-wrapper {
  flex-shrink: 0;
  overflow-y: auto;
}

.table-body {
  position: relative;
}

.table-row {
  display: flex;
  border-bottom: 1px solid;
}

.table-row-cell {
  flex-shrink: 0;
  width: 100px;
  padding: 8px 4px;
}

.table-row-cell,
.table-header-cell {
  border-right: 1px solid;
}
